.transition {
    width: 100vw;
    height: 200px;
    border-top: solid 1px transparent;
    border-bottom: solid 1px transparent;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    >img {
        object-fit: cover;
        width: 100%;
        height:100%;
    }
}